body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html, div, blockquote, img, label, p, h1, h2, h3, h4, h5, h6, pre, ul, ol,
li, dl, dt, dd, form, a, fieldset, th, td
{
margin: 0; padding: 0; border: 0; outline: none;
}
 
body
{
line-height: 1;
font-size: 88% /* Decide for yourself if you want to include this. */;
}
 
h1, h2, h3, h4, h5, h6
{
font-size: 100%;
padding: .6em 0;
margin: 0 15px;
}
 
ul, ol
{
list-style: none;
}
 
a
{
color: black;
text-decoration: none;
}
 
a:hover
{
text-decoration: none;
}

.app {
  width: 800px;
  margin: 0 auto;
  margin-top: 32px;
  padding: 16px;
  border: 1px solid;
}

.box {
  width: 100%;
  height: 300px;
  max-width: 320px;
}

.btn-submit {
  background-color: #F2F1EF;
  color: black;
  font-size: 12px;
}

.title-ligth-grey {
  color: rgb(77, 77, 77);
  padding: 4px;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.regular {
  padding: 4px;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
}

.flex-around {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 8px;
}


.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.center {
  text-align: center;
  font-size: 16px;
}
.wrapper-cards {
  margin: 0 auto;
  max-width: 100%;
  margin-right: 20px;
  margin-left: 20px;
}

.wrapper-forms {
  border-bottom: 1px solid;
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}
.card {
  border: 1px solid;
  padding: 8px;
  margin-bottom: 16px;
  width: 330px;
}
.card-header {
  padding: 8px;
  border-bottom: 1px solid;
  margin-bottom: 8px;
}
.card-title {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: bold;
}

.btn-close {
  background-color: #f0f0f0;
  border: none;
  font-size: 16px;
}
input {
  width: 270px;
  padding: 3px;
  border-radius: 5px;
}

label {
  font-size: 14px;
  padding: 2px;
  font-weight: bold;
}


.wrapper-input {
  margin-bottom: 16px;
  margin-top: 8px;
}

.menu {
  margin-top: 2px;
  margin-bottom: 12px;
  width: 270px;
  background-color: #ffffff;
  height: 25px;
  border-radius: 4px;
}
.office {
  width: 100%;
}

.office-title {
  font-size: 24px;
}

.office-header {
  height: 35px;
  border-bottom: 1px solid;
  margin-bottom: 16px;
}

.office-detail > .title-ligth-grey {
  font-size: 18px;
}
.office-detail > .regular {
  font-size: 16px;
}

.btn-navigate {
  float: right;
  height: 19px;
  width: 116px;
  padding: 8px;
  border: 1px solid lightgray;
}



